/* eslint-disable @typescript-eslint/no-explicit-any */
import useDocs from "@/hooks/useDocs";
import dayjs from "dayjs";
import { orderBy, Timestamp, where } from "firebase/firestore";
import { useSession } from "next-auth/react";
import { useRouter } from "next/router";
import { FC } from "react";

interface Ticket {
  id: string;
  created_at: Timestamp;
  status: string;
  topic: string;
  description?: string;
  user_id: string;
  username: string;
}

const TicketsList = () => {
  const { data } = useSession();
  const ticketsOpened: Ticket[] =
    useDocs(
      "tickets",
      null,
      where("user_id", "==", data?.user?.id),
      where("status", "==", "opened"),
      orderBy("created_at", "desc")
    ) || [];

  return (
    <div className="flex flex-col gap-12">
      <div className="flex flex-col gap-4">
        <span className="text-2xl">Tickets Abiertos</span>
        <div className="grid grid-cols-4 gap-4">
          {ticketsOpened.map((ticket, index) => (
            <CardTicket key={index} ticket={ticket} />
          ))}
        </div>
      </div>
    </div>
  );
};

export default TicketsList;

const CardTicket: FC<{ ticket: Ticket }> = ({ ticket }) => {
  const router = useRouter();

  const fecha = (date: any) => {
    if (!date) return "Fecha no disponible";

    const firebaseDate = date?.seconds ? new Date(date.seconds * 1000) : date;
    const newFecha = dayjs(firebaseDate).format("DD/MM/YY h:mm A");
    return newFecha;
  };

  return (
    <div
      className="flex flex-col rounded-md shadow-md p-4 text-white cursor-pointer bg-zinc-600"
      onClick={() => router.push(`/support/${ticket.id}`)}
    >
      <div>
        <span className="font-bold text-lg text-primary">{ticket?.topic}</span>
      </div>
      <div className="flex justify-between gap-4">
        <p className="text-left text-sm">{ticket?.description}</p>
        <span className="font-thin text-sm">{fecha(ticket?.created_at)}</span>
      </div>
    </div>
  );
};
